@import "../style.scss";

/** 
* Styles for modals with a list.
* Increases textarea size and adds a small margin to the top and bottom.
* Prevents the user from resizing
*/
.listModal {
    margin: 8px 0px;
}
.listModal textarea {
    height: 90px;
    resize: none;
    color: black;
}
.listModal ::-webkit-scrollbar-thumb {
    min-height: auto;
}
