@import "../../style.scss";
@import "../../generic-components/card/CardSpacingBelow.scss";

/* Overall tab content layout */
.cuttingDataContainer {
    flex-grow: 1;
    display: flex;
    flex-direction: row;

    /* Preset selection on the left */
    .cuttingDataTableContainer {
        flex: 1 0 0;
        padding-right: $density-spacings-extraExtraLarge;
        min-height: 300px;
        min-width: 100px;
    }
    /* Feeds & speed settings on the right */
    .cuttingDataParameters {
        flex: 2 1;
        display: flex;
        flex-direction: column;
    }
}
