@import "../style.scss";

.MatrixTable__table {
    background-color: $colorScheme-surfaceLevel100Color !important;
    color: $colorScheme-textColor !important;
    font-size: 12.5px !important;
}

// Entire table header
.MatrixTable__header,
.MatrixTable__header-row {
    border-bottom: 1px !important;
}

// All rows.
.MatrixTable__container .MatrixTable__header-row,
.MatrixTable__container .MatrixTable__row:not(.MatrixTable__row--depth-0) {
    border-bottom: 0px !important;
    img:focus {
        outline: none;
    }
}

// Not selected, hovered.
.MatrixTable__container .MatrixTable__row:hover,
.MatrixTable__container .MatrixTable__row--hovered {
    background-color: $basics-colors-charcoal100 !important;
}

// Selected, not hovered
.MatrixTable__container .MatrixTable__row--selected {
    background-color: $basics-colors-autodeskBlue200 !important;
}

// Selected, hovered
.MatrixTable__container
    .MatrixTable__row--selected:hover.MatrixTable__row--hovered.MatrixTable__row:not(.MatrixTable__group-header),
.MatrixTable__container
    .MatrixTable__row--selected:hover.MatrixTable__row:hover.MatrixTable__row:not(.MatrixTable__group-header) {
    background-color: $basics-colors-autodeskBlue100 !important;
}

// Ensure the tooltip uses Artifakt
.EllipsisText__tooltip {
    font-family: "ArtifaktElement", Arial, Helvetica, sans-serif;
    -webkit-text-stroke: 0;
}
