@import "../style.scss";
@import "../elements/ErrorText.scss";

.cuttingDataTable {
    /* extend the name column to fill the entire row */
    .MatrixTable__row-cell {
        width: 100% !important;
    }

    .cuttingDataName,
    .cuttingDataNameError {
        cursor: default;

        /* expand to fill the space so that the tooltip
           appears if you hover anywhere in the row */
        width: 100%;

        /* trail into ... when text is too long */
        overflow: hidden;
        text-overflow: ellipsis !important;
        white-space: nowrap;
    }
    .cuttingDataNameError {
        @extend %error-text;
    }
}
