@import "../../style.scss";

/* Main cutter tab contents */
.cutterTab {
    flex: 1 0 0;
    display: flex;
}
/* Settings cards on the left */
.cutterTab > .cutterTabSettings {
    flex: 1 0 0;
}
/* Geometry diagram to the right */
.cutterTab > .cutterTabGeometryDiagram {
    flex: 1 1 0;
    position: relative;
    overflow: hidden;
    min-width: 200px;
    min-height: 400px;
}
/* SVG for geometry diagram */
.cutterTab > .cutterTabGeometryDiagram > [class|="toolEditDiagram"] {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
}
/* Rotate SVGs which are orientated to the left to point down */
.cutterTab > .cutterTabGeometryDiagram > .toolEditDiagram-left {
    transform: rotate(-90deg);
    overflow: hidden;
}
