@import "../style.scss";
@import "../elements/ErrorText.scss";

/* Overall style for the Edit tool page */
#EditTool {
    display: flex;
    flex-direction: column;
    height: 100%;
    background-color: $basics-colors-white;
    padding-left: $density-spacings-extraExtraLarge;
    padding-right: $density-spacings-extraExtraLarge;
    overflow-x: auto;

    /* Everything on the page except for the controls */
    > .pageContents {
        flex-grow: 1;
        display: flex;
        overflow-y: hidden;

        /* Tabs & contents to the left */
        > .toolSettingsPane {
            display: flex;
            flex-direction: column;
            width: 100%;

            /** Tabs and contents to the left inner div */
            > div[class^="css"] {
                flex: 1;
                padding: $density-spacings-small;
                overflow: auto;

                /* The tabs themselves */
                > ul {
                    flex: 0 0 auto;
                    justify-content: flex-start;

                    li.invalidTab {
                        .invalidTab__label {
                            @extend %error-text;
                        }
                    }
                }

                /* The contents except for the tabs */
                > div {
                    display: flex;
                    padding-top: $density-spacings-extraSmall;
                    min-width: 400px;
                    /* Full page edit cards */
                    > .card {
                        flex: 1;
                        width: 100%;
                    }
                }
            }
        }

        /* Tool preview to the right */
        > .toolPreviewPane {
            display: flex;
            min-width: 0; /* https://stackoverflow.com/a/34145461 */

            > .toolPreview {
                flex: 1;
                display: flex;
                width: 100%;
                margin-top: 10px;

                > div {
                    flex: 1;
                    width: 100%;
                }
            }
        }
    }
}
