@import "../style.scss";

%problem-text {
    font-style: italic;
}

/** Warning image after some text - place on <svg> */
%warning-text {
    @extend %problem-text;

    color: $basics-colors-warning;
}

/** Error image after some text - place on <svg> */
%error-text {
    @extend %problem-text;

    color: $basics-colors-error;
}
