.orientationContainer {
    position: relative;
    width: 100px;
    height: 100px;
}

.orientationContainer > .up {
    position: absolute;
    left: 36px;
    top: 0px;
}

.orientationContainer > .down {
    position: absolute;
    left: 36px;
    top: 72px;
}

.orientationContainer > .left {
    position: absolute;
    left: 0px;
    top: 36px;
}

.orientationContainer > .right {
    position: absolute;
    left: 72px;
    top: 36px;
}
