@import "../style.scss";

%svg-icon {
    min-width: $tabs-general-icon-size;
    min-height: $tabs-general-icon-size;
}

/** Warning image after some text - place on <svg> */
%warning-svg {
    @extend %svg-icon;

    * {
        fill: $basics-colors-warning;
    }
}

/** Error image after some text - place on <svg> */
%error-svg {
    @extend %svg-icon;

    * {
        fill: $basics-colors-error;
    }
}
