@import "./style.scss";

/**
 * These are Matrix Table colours rather HIG colours.
 *
 * Preferably we would use HIG theme variables,
 * but that looks inconsistent next to the table.
 * We should customise the table border to use HIG colours.
 */
$matrix-divider-lightColor: #edf0f2;
$matrix-colors-surfaceLightGrayShadow: #81909930;
$matrix-border: $divider-width solid $matrix-divider-lightColor;

// The border style used by the Matrix Table.
@mixin matrix-border {
    box-shadow: 0 2px 4px 0 $matrix-colors-surfaceLightGrayShadow;
    outline: $matrix-border;
}
