/** Container element for System Error card */
[data-cy="errorHandler"] {
    height: 100vh;
    overflow: auto;
    width: 100vw;

    /** Main card surface */
    > div {
        .errorHeader {
            align-items: center;
            display: flex;
            flex-direction: column;
        }

        margin: 40px;

        /** Buttons */
        [data-cy="controls"] {
            text-align: center;

            > div {
                display: inline-block;
            }
        }

        /** Error details */
        textarea {
            min-height: 100px;
            max-height: 1000px;
        }
    }

    /** Development-mode-only information box */
    > [data-cy="debugInfo"] {
        display: block;
        font-family: "Lucida Console";
        margin: 20px;
        padding: 20px;
        text-align: center;
    }
}
