@import "../../style.scss";
@import "../../matrixBorder.scss";

/** Container for tab bar and all tabbed components */
.infoPanelTabs {
    display: flex;
    flex-direction: column;
    flex: 1 1 100%;
    overflow-y: auto;
    @include matrix-border;
}

.infoPanelTabs > div,
.infoPanelTabs > div > div {
    overflow-y: auto;
    flex: 1 0 0;
}
