@import "../style.scss";

.toolManagerContainer {
    display: flex;
    flex-direction: column;
    height: 100%;
    min-width: 640px;
    min-height: 300px;
}
.toolManagerLowerContainer {
    display: flex;
    flex: 1 1 auto;
    flex-direction: row;
    min-height: 0; /* https://stackoverflow.com/a/34145461 */
    padding: 0;
}
.toolManagerControls {
    padding: 0 $density-spacings-extraExtraLarge;
}
.toolManagerLibraries,
.toolManagerTools,
.toolManagerInfo {
    display: flex;
    flex-direction: column;
    background-color: $colorScheme-surfaceLevel100Color;
}
.toolManagerLibraries > .container,
.toolManagerTools > .container,
.toolManagerInfo > .container {
    flex: 0 0 40px;
}
.toolManagerLibraries > .contextMenusContainer,
.toolManagerTools > .toolManagerToolList,
.toolManagerInfo {
    flex: 1 1 auto;
    min-height: 0; /* https://stackoverflow.com/a/34145461 */
}
.toolManagerInfo > .contextMenusContainer {
    flex: 1 1 0;
    min-height: 0; /* https://stackoverflow.com/a/34145461 */
}
.toolManagerTools > .toolManagerToolList {
    display: flex;
    flex-direction: column;
}
.toolManagerTools > .toolManagerToolList > .contextMenusContainer {
    flex: 1 1 auto;
}
.toolManagerTools > .toolManagerCuttingDataPresetList {
    flex: 0 0 200px;
}
.toolManagerInfo > .toolPreview {
    display: flex;
    flex: 0 0 40%;
    max-height: 50%;
    min-height: 0; /* https://stackoverflow.com/a/34145461 */
    margin-right: 10px;
}
.toolManagerInfo > .toolPreview > * {
    flex: 1;
    width: 100%;
}
.toolManagerTools > .toolManagerCuttingDataPresetList,
.toolManagerInfo > .toolPreview {
    padding-top: $density-spacings-large;
}
.gutter-horizontal {
    cursor: ew-resize;
}
.gutter-vertical {
    cursor: ns-resize;
}
.toolManagerLists {
    flex: 1 1 0;
    min-height: 0; /* https://stackoverflow.com/a/34145461 */
    display: flex;
    flex-direction: column;
}
