@import "../../style.scss";

/* Main tab contents */
.turningSetupTab {
    flex: 1 0 0;
}
/* Contents of the settings card */
.turningSetupTab * .turningSetupTabOrientation {
    display: flex;
    flex-direction: row;
}
/* Edit fields on the left of the card */
.turningSetupTab * .turningSetupTabOrientation > :not(.orientationContainer) {
    flex: 1 0 0;
    margin-right: $density-spacings-extraExtraLarge;
}
/* Orientation arrow buttons on the right of the card */
.turningSetupTab * .turningSetupTabOrientation > .orientationContainer {
    flex: 0 0 auto;
}
