@import "../../style.scss";

/* Main tab contents */
.shaftTab,
.holderGeometryTab {
    flex: 1 0 300px;
    min-height: 0; /* https://stackoverflow.com/a/34145461 */
}
/* Main holder geometry tab contents */
.holderGeometryTab {
    display: flex;
    flex-direction: column;
}
/* Holder settings above the segment list */
.holderGeometryTab > .holderGeometrySettings {
    flex: 0 0 auto;
    padding-bottom: $density-spacings-large;
}
/* Holder segment list */
.holderGeometryTab > .contextMenusContainer {
    flex: 1 0 300px;
    min-height: 0; /* https://stackoverflow.com/a/34145461 */
}
/* Hide labels next to edit fields */
.shaftTab > .contextMenusContainer * .editField,
.holderGeometryTab > .contextMenusContainer * .editField {
    width: 100%;
}
/* Hide labels next to edit fields */
.shaftTab > .contextMenusContainer * .labelledInput > label,
.holderGeometryTab > .contextMenusContainer * .labelledInput > label {
    display: none;
}
