@import "../../style.scss";
@import "../../matrixBorder.scss";

/* Action menu with buttons that do things at the top of a component */
.headerMenu {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;

    /**
     * This is a bit of a hack:
     * The elements below use an outline. Outline is a border outside
     * of your own space, and so that oultine takes up one divider width of this header.
     * Add a margin to reclaim that space.
     */
    margin-bottom: $divider-width;

    @include matrix-border;

    .leftItems,
    .rightItems {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;

        .headerMenuItem {
            margin: $density-spacings-extraSmall;
        }

        .headerMenuItemDivider {
            border-left: $divider-width solid $basics-colors-charcoal200;
            margin-top: $density-spacings-extraExtraSmall;
            margin-bottom: $density-spacings-extraExtraSmall;
            margin-left: $density-spacings-extraSmall + 2px;
            margin-right: $density-spacings-extraSmall;
            width: 0;
        }
    }

    .leftItems {
        justify-content: flex-start;
    }

    .rightItems {
        justify-content: flex-end;
    }

    .headerMenuItem {
        svg * {
            fill: $iconButton-on-iconColor;
        }
    }
    .disabled {
        svg * {
            fill: $iconButton-iconColor;
        };
    }
}
