@import "../../style.scss";
@import "../../matrixBorder.scss";

.propertyContainer {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
    margin-bottom: 6px;
    justify-content: space-between;
}

.propertyKey {
    min-width: 50%;
}

.propertyValue {
    flex-grow: 1;
}

#contextMenu {
    width: 100%;
    height: 26px;
}

/**
* These stylings change the height of the Parameter info panel and ToolPreview
* panel. Using a percentage of the parent's height doesn't work as ToolPreview
* inherits a height of 0.
*
* By using 50% of the viewport height minus a fixed pixel height offset to
* account for .rightcontainer, the tabs, the bottom 'Accept', 'Cancel'
* button bar (and any applicable padding), we come close to approximating 50%
* of the parent container height.
*/
#sidePanel {
    height: 100%;
    display: flex;
    flex-direction: column;
    overflow: hidden;
}

#infoPanel {
    flex: 1;
    padding-top: $density-spacings-extraSmall;
    padding-right: $density-spacings-medium;
    padding-left: $density-spacings-medium;
    padding-bottom: $density-spacings-extraSmall;
    background-color: $colorScheme-surfaceLevel100Color;
    overflow-y: scroll;
    border-bottom: $matrix-border;
}

#previewPanel {
    display: flex;
    flex: 1;
    min-width: 1px;
    .toolPreview {
        #container {
            flex: 1;
            width: 100%;
        }
        display: flex;
        width: 100%;
    }
}
