@import "../../style.scss";

.menuItem {
    display: flex;
    flex-direction: row;
    border-left: solid 2px transparent;
    cursor: pointer;
    align-items: center;
}

.menuItem * {
    padding: 4px;
}

.menuItem-enabled svg * {
    fill: $iconButton-on-iconColor;
}

.menuItem-enabled:hover {
    background-color: $basics-colors-charcoal100;
}

.menuItem-disabled svg * {
    fill: #bec8d2;
}
