@import "../../style.scss";
@import "../../generic-components/card/CardSpacingBelow.scss";

/* Edit fields on cards except for the last one */
#EditTool * .card * .editField:not(:last-child) {
    margin-bottom: $density-spacings-medium;
}

#EditTool * .card {
    @extend %card-spacing-below;
}

#EditTool * .card:first-of-type {
    margin-top: $density-spacings-small;
}
