@import "./style.scss";
@import "./matrixBorder.scss";

// HACK: CAM-21454
// Since the Qt 5.12 update, text is sometimes invisible in the QtWebEngine.
// Applying these specific styles fixes it for some reason.
//
body {
  opacity: 0.99999;
}

:not(input) {
    user-select: none;
}

// Increase text legibility at different scaling levels.
// 100% scaling
@media (-webkit-max-device-pixel-ratio: 1) {
    body {
        -webkit-text-stroke: 0.016rem;
    }
}

// 125% scaling
@media (-webkit-min-device-pixel-ratio: 1.25) {
    body {
        -webkit-text-stroke: 0.024rem;
    }
}

// 150+% scaling. OSX defaults to 200%
@media (-webkit-min-device-pixel-ratio: 1.5) {
    body {
        -webkit-text-stroke: 0.032rem;
    }
}

body {
    border-top: $divider-width solid $matrix-divider-lightColor;
    // Prevent overscrolling / rubber-banding on Mac
    overscroll-behavior: none;
}

body,
#root,
#root > #router {
    background-color: $colorScheme-surfaceLevel100Color;
    height: 100vh;
    width: 100%;
    margin: 0;
    position: fixed;
}

#root > div[role="progressbar"] {
    height: 100%;
    vertical-align: middle;
}

/** Prevent user from dragging images */
img {
    -webkit-user-drag: none;
}

/** Accept / cancel button section at the bottom of the page */
#pageControls {
    margin-top: $density-spacings-extraSmall;
    height: $density-spacings-extraExtraLarge;
    display: flex;
    justify-content: flex-end;
    flex-shrink: 0;
}
