@import "../../style.scss";

/**
 * Tool summary in the top-left of the Edit page
 * On a single line, not allowed to change size
 */
.toolSummary {
    display: flex;
    flex: 0 0 $input-height;
    width: 100%;
    height: $input-height;
    overflow: hidden;
    padding-top: $density-spacings-extraSmall;
    padding-left: $density-spacings-small;
    padding-right: $density-spacings-small;
}
/* Each section of the summary */
.toolSummary > .breadcrumb {
    color: $tooltip-textColor;
    display: flex;
    flex: 0 1 auto;
    min-width: 0;
}
/* Don't let the library name get squashed */
.toolSummary > .breadcrumb[data-cy="tool-summary-library"] {
    flex: 0 1 0;
    min-width: min-content;
}
/* Show a divider between each breadcrumb */
.toolSummary > .breadcrumb:not(:nth-last-child(1)):after {
    content: "/";
    color: $typography-caption-color;
    font-size: $typography-caption-fontSize;
    font-family: $typography-caption-fontFamily;
    line-height: $input-height;
    padding-left: $density-spacings-small;
    padding-right: $density-spacings-small;
}
/** Tool type icon */
.toolSummary > .breadcrumb > img {
    flex: 0 0 $input-height;
    height: $input-height;
    padding-right: $density-spacings-small;
}
/** Tool description */
.toolSummary > .breadcrumb > p {
    flex: 1 1 0;
    line-height: $input-height;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
