@import "../../style.scss";

/* Overall tab content layout */
.insertTab {
    flex-grow: 1;
    display: flex;
    flex-direction: row;
}

/* Settings cards on the left */
.insertTabSettings {
    flex: 1 0 0;
}

/* Geometry diagram to the right */
.insertTabGeometryDiagram {
    flex: 1 1 0;
    min-width: 200px;
    min-height: 400px;
    position: relative;
    overflow: hidden;
}

/* SVG for geometry diagram */
.insertTabGeometryDiagram > [class|="toolEditDiagram"] {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
}
