@import "../style.scss";

.labelledInput {
    display: flex;
    align-items: center;
}
.labelledInput.left {
    flex-direction: row-reverse;
    justify-content: flex-end;
}
.labelledInput.left > label {
    padding-left: $density-spacings-small;
}
.labelledInput.right > label {
    flex: 0 1 200px;
    margin-right: $density-spacings-small;
    overflow-x: auto;
    white-space: nowrap;
}
.labelledInput.right > :not(label) {
    flex: 1 0 150px;
}
