@import "../../../style.scss";

.rangeTextInputs {
    display: flex;
    flex-direction: row;
}

.minInput,
.maxInput {
    flex: 1 1 0;
}

.dash {
    flex: 0 0 auto;
    padding: 0 $density-spacings-extraSmall;

    /* Center the dash */
    height: $basics-fontSizes-lowLarge;
    display: flex;
    flex-direction: row;

    * {
        align-self: center;
    }
}
