@import "../../../style.scss";

.numberFilter {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-top: -$density-spacings-small;
    flex-grow: 1;
    margin-right: -$density-spacings-medium;
    overflow-y: visible;
}
.numberFilterDropdown {
    margin-right: $density-spacings-medium;
    flex: 1 0 70px;
    margin-top: $density-spacings-small;
}
.numberFilterInputs {
    margin-right: $density-spacings-medium;
    margin-top: $density-spacings-small;
    flex: 2 0 140px;
}
