@import "../../style.scss";

/* Overall tab content layout */
.holderSelectContainer {
    flex-grow: 1;
    display: flex;
    flex-direction: row;
    min-width: 600px;

    /* Left hand pane */
    > .holderSelectFilter {
        flex: 1 0 0;
        padding-right: $density-spacings-extraExtraLarge;
        min-width: 100px;

        /* Table of holders on the left below the search bar */
        > .holderSelectTools {
            height: calc(100% - 40px);
            min-height: 300px;

            .ExpandIcon__icon {
                // Keep cursor the same so the icon doesn't look interactable
                cursor: default;
                // Grey out icons so they don't look interactable
                opacity: 0.2;
            }
        }
    }

    /* Right hand pane */
    > .holderSelectDetails {
        flex: 1 0 auto;

        /* Holder info settings in the top right*/
        > .holderSelectHeader {
            padding-bottom: 10px;
            margin-right: 10px;
            flex-grow: 1;
        }

        /* Free floating buttons below the info settings */
        > .holderSelectControls {
            padding-top: $density-spacings-extraLarge;

            > div {
                display: inline-block;
            }
        }
    }
}
