@import "../../style.scss";

.iconButton {
    border: 1px solid $basics-colors-autodeskBlue200;
    border-radius: $basics-borderRadii-large;
    box-sizing: inherit;
    display: block;
    height: auto;
    text-align: center;
    min-width: 110px;
    margin: 1px;
    background: $basics-colors-white;
}

.iconButton:hover {
    background: $basics-colors-charcoal100;
}

.iconButton:active,
.depressed {
    background: $basics-colors-autodeskBlue200;
}

.depressed:hover {
    background: $basics-colors-autodeskBlue100;
}

.iconButtonIcon {
    display: block;
    height: 30px;
    margin: 0 auto;
    padding: $density-spacings-extraExtraSmall;
    width: 30px;
}

.iconButtonText {
    display: block;
    padding: $density-spacings-extraExtraSmall;
    font-family: $label-fontFamily;
    font-size: $density-fontSizes-medium;
}
