@import "../style.scss";
@import "../elements/ErrorSVG.scss";

.modalContainer {
    display: flex;
    flex-direction: column;
    overflow: hidden;
    flex: 1 1 auto;

    .modalContentContainer {
        flex-grow: 1;
        flex-shrink: 1;
        overflow: auto;
    }

    .modalButtonsContainer {
        padding-top: $density-spacings-small;
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        flex-shrink: 0;
        flex-grow: 0;
    }
}

header[id|="modal"] {
    .header {
        border-bottom: $divider-width solid $divider-heavyColor;
        display: flex;
        justify-content: space-between;
        align-content: center;
        padding: 16px;
    }

    .title {
        display: flex;
        justify-content: flex-start;
        align-items: center;

        .title-alert {
            @extend %warning-svg;
            padding-right: $density-spacings-small;
        }
    }
}
