@import "../style.scss";
@import "../elements/ErrorText.scss";

/**
* Hacks to place the settings dropdown on the left hand side of the tool list
* in the tool icon columns header.
*/

.toolManagerTools {
    // Hide the frozen column created for the settings icon, and place it on
    // the left instead of the right.
    .MatrixTable__table.MatrixTable__table-frozen-right {
        visibility: hidden;
        left: 0;
        // Unhide the header and icon again
        .MatrixTable__header {
            visibility: visible;
        }
    }

    // Centre the settings icon in the tool icon column
    .MatrixTable__header-cell.MatrixTable__header-cell--align-right {
        width: 50px !important;
    }

    // Flip the settings panel so that it dropdown to the right of the settings
    // icon instead of the left
    .MatrixTable__settings.Popover {
        transform: scaleX(-1) translate3d(-3px, 33px, 0px) !important;
    }
    .SettingsPanel {
        transform: scaleX(-1);
    }
    .Popover__arrow {
        right: 7px;
        left: unset !important;
    }

    // Styling for invalid/incomplete tools
    .incompleteTool, .invalidTool {
        /* table cells usually have these flex settings,
           but the custom div is between rows & cells in the tree hierarchy */
        display: flex;
        flex: 1 0 0;
    }
    .incompleteTool {
        @extend %warning-text;
    }
    .invalidTool {
        @extend %error-text;
    }
}
