@import "../style.scss";

.ToolList {
    // First column (icons)
    .MatrixTable__row-cell:first-child {
        // Remove padding after expand icons
        padding-left: 0 !important;
    }

    // Depth 0 - library
    .MatrixTable__row.tableRow.MatrixTable__row--depth-0 {
        border-bottom: 1px !important;
        box-shadow: inset 0px 2px 4px rgba(0, 0, 0, 0.05);
        color: $basics-colors-charcoal800 !important;

        // from matrix table header style
        background-color: #f7f9fa;
        font-weight: 700;

        .MatrixTable__row-cell:nth-child(2) {
            // Unset width so text doesn't get truncated
            width: initial !important;
        }
    }

    // Depth 1 - tool
    .MatrixTable__row.tableRow.MatrixTable__row--depth-1 {
        .MatrixTable__row-cell {
            .ExpandIcon {
                // Remove margin so tool icon is visible
                margin-left: 0 !important;
            }

            // Don't use text select cursor as the text isn't selectable
            cursor: default;
        }
    }

    // Depth 2 - operation
    .MatrixTable__row.tableRow.MatrixTable__row--depth-2 {
        // Extra indentation for readability
        // Align icon with the start of the tool description after the tool number
        margin-left: 28px;

        .MatrixTable__row-cell {
            // Unset width so the strategy icon doesn't get cut off
            width: initial !important;
        }
    }
}
