@import "../../style.scss";
@import "../../matrixBorder.scss";
@import "../../elements/ErrorText.scss";

.treeContainer {
    @include matrix-border;
    background-color: white;
    overflow: auto;
    width: 100%;

    /* see class names in react-treeview.css */
    .tree-view {
        min-width: min-content;
        overflow: hidden;
        white-space: nowrap;
        width: 100%;
    }
    .tree-view_arrow {
        display: none;
    }
    .tree-view_children {
        width: calc(100% - 21px); /* full width minus the margin */
        margin-left: 21px;
    }
    .tree-view_leaf {
        margin-left: $density-spacings-large;
    }
    .tree-view_leaf > div {
        padding: 3px;
    }
    .tree-view_item {
        width: 100%;
        height: $density-spacings-extraLarge;
        padding: 0 $density-spacings-extraExtraSmall;
        overflow: visible;
        margin: 2px;
    }
    .tree-view_item:hover {
        cursor: pointer;
        background: $basics-colors-charcoal100;
        z-index: 1000;
        margin: 2px;
    }

    /* our custom specifiers */
    .tree-view_item_selected {
        background: $basics-colors-autodeskBlue200;
    }
    .tree-view_item_selected:hover {
        background: $basics-colors-autodeskBlue100;
    }
    [data-cy="tree-view-arrow"],
    [data-cy="tree-view-label"] {
        display: inline-block;
        vertical-align: middle;
    }
    [data-cy="tree-view-arrow"] {
        /* leave a little space before the text so the arrow stands out */
        margin-right: $density-spacings-extraSmall;
    }

    [data-cy="tree-view-label"] {
        div,
        p {
            display: inline-block;
            vertical-align: middle;
        }
    }
    .treeLabelError > p {
        @extend %error-text;
    }
    .treeLabelWarning > p {
        @extend %warning-text;
    }
    [data-cy="tree-scroll"] {
        height: 100%;
        overflow: auto;
        position: relative;
        width: 100%;
    }
    [data-cy="tree-scroll"] > div {
        /* without this extra space, the last library gets cut off when the scroll bar
       is visible for some reason */
        margin-bottom: 25px;
    }

    .editingLabelWrapper {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: flex-start;
        align-items: center;
    }
}
